         a.select2-choice {\n                border: 0;\n                padding: 0;\n                box-shadow: none;\n                height: 100%;\n                padding: 0 5px;\n\n                &,\n                &:hover {\n                    background-color: transparent;\n                    background-image: none;\n                }\n                .select2-arrow {\n                    border: 0;\n                    background: none;\n                }\n            }\n        }\n    }\n\n    .mosaic-button-group {\n        margin-left:1rem;\n        padding-left:1rem;\n        border-left:var(--bs-border-width) var(--bs-border-style) var(--bs-border-color);\n    }\n}\n\n// with enabled edit-bar\nbody.plone-toolbar-left {\n    > .mosaic-toolbar {\n        margin-left: var(--plone-toolbar-width-collaped);\n    }\n    &.plone-toolbar-left-expanded {\n        > .mosaic-toolbar {\n            margin-left: var(--plone-toolbar-width);\n        }\n    }\n}\n\n.mosaic-toolbar-content {\n    display: flex;\n    flex-flow: wrap;\n    justify-content: space-between;\n}\n\n.mosaic-toolbar-secondary-functions {\n    box-shadow: none;\n}\n\n/* Hide mosaic-menu labels with select2 */\n.mosaic-enabled .select2-results {\n    max-height: none;\n    width: 100%;\n    li.select2-result.select2-option-none {\n        display: none !important;\n    }\n}\n\n.select2-drop {\n    &.mosaic-menu-insert,\n    &.mosaic-menu-format {\n        ul.select2-results {\n            display: flex;\n\n            .select2-result-sub {\n                .select2-result-unselectable,\n                .select2-disabled {\n                    background: var(--bs-secondary-bg);\n\n                    > .select2-result-label {\n                        color: var(--bs-body-color-secondary);\n                        cursor: default;\n                        opacity: 0.5;\n                    }\n                }\n            }\n            .select2-result-label {\n                white-space: no